// animation-handler.js
var AnimationHandler = pc.createScript('animationHandler');

AnimationHandler.attributes.add("anims", {
    type: "json",
    schema: [
        {
            name: "btn",
            type: "entity"
        }, {
            name: "target",
            type: "entity"
        }, {
            name: "slot_out",
            type: "string"
        }, {
            name: "slot_in",
            type: "string"
        }, {
            name: "camPos",
            type: "entity"
        }
    ],
    array: true
});

AnimationHandler.attributes.add("camera", {
    type: "entity"
});

AnimationHandler.attributes.add("btnSoundSlot", {
    type: "string"
});

AnimationHandler.attributes.add("trigger_out", {
    type: "string"
});

AnimationHandler.attributes.add("trigger_in", {
    type: "string"
});

// initialize code called once per entity
AnimationHandler.prototype.initialize = function () {
    this.soundSrc = this.entity.sound;
    this.camAnim = this.camera.script.cameraAnimation;

    this.isPlaying = false;

    for (let i = 0; i < this.anims.length; i++) {
        this.anims[i].btn.on("btn:pressed", () => {
            this.soundSrc.play(this.btnSoundSlot);
            if (this.isPlaying)
                return;
            if (this.active == undefined) {
                this.active = this.anims[i];
                this.anims[i].target.fire(this.trigger_out);
                this.anims[i].target.anim.once("anim:end", () => this.isPlaying = false, this);
                this.camAnim.moveTo(this.anims[i].camPos)
                this.entity.script.animatePosition.enabled = false;
                this.soundSrc.play(this.anims[i].slot_out);
            }
            else if (this.active == this.anims[i]) {
                this.active.target.anim.once("anim:end", () => {
                    this.active = undefined;
                    this.isPlaying = false;
                }, this);
                this.active.target.fire(this.trigger_in);
                this.soundSrc.play(this.active.slot_in);
                this.camAnim.reset();
                this.entity.script.animatePosition.enabled = true;

            } else {
                this.active.target.anim.once("anim:end", () => {
                    this.active = this.anims[i];
                    this.anims[i].target.fire(this.trigger_out);
                    this.anims[i].target.anim.once("anim:end", () => this.isPlaying = false, this);
                    this.camAnim.moveTo(this.anims[i].camPos)
                    this.entity.script.animatePosition.enabled = false;

                    this.soundSrc.play(this.active.slot_out);
                }, this);
                this.active.target.fire(this.trigger_in);
                this.soundSrc.play(this.active.slot_in);
                this.camAnim.reset();
                this.entity.script.animatePosition.enabled = true;

            }

            this.isPlaying = true;
        }, this);
    }

};

// update code called every frame
AnimationHandler.prototype.update = function (dt) {

};

