// frame-on-event.js
var FrameOnEvent = pc.createScript('frameOnEvent');

FrameOnEvent.attributes.add("zoomInEvent", {
    type: "string"
});

FrameOnEvent.attributes.add("zoomOutEvent", {
    type: "string"
});

FrameOnEvent.attributes.add("camera", {
    type: "entity"
});

FrameOnEvent.attributes.add("zoomPosition", {
    type: "entity"
});

// initialize code called once per entity
FrameOnEvent.prototype.initialize = function () {
    this.initialPos = this.camera.getLocalPosition().clone();
    this.initialRot = this.camera.getLocalRotation().clone();
    this.currentState = false;

    this.entity.on(this.zoomInEvent, () => this.frame(true), this);

    this.entity.on(this.zoomOutEvent, () => this.frame(false), this);

    this.on('destroy', function () {
        this.entity.off(this.zoomInEvent);
        this.entity.off(this.zoomOutEvent);

    }, this);
};

FrameOnEvent.prototype.enableOrbitScripts = function (bool) {
    this.camera.script.orbitCamera.enabled = bool;
    this.camera.script.mouseInput.enabled = bool;
    this.camera.script.touchInput.enabled = bool;
};

FrameOnEvent.prototype.frame = function (state) {
    if (this.currentState == state)
        return;
    this.currentState = state;
    if (state) {
        this.initialRot = this.camera.getLocalRotation().clone();
        this.initialPos = this.camera.getLocalPosition().clone();
        this.enableOrbitScripts(false);
    }
    let target = state ? this.zoomPosition.getLocalPosition() : this.initialPos;
    let targetRot = state ? this.zoomPosition.getLocalRotation() : this.initialRot;
    this.camera
        .tween(this.camera.getLocalPosition())
        .to(target, 1.0, pc.SineOut)
        .on("complete", () => this.enableOrbitScripts(!state), this)
        .start();
    this.camera.tween(this.camera.getLocalRotation()).to(targetRot, 1.0, pc.SineOut)
        .start();
    this.entity.fire("iframe:interactable", state);
};

// update code called every frame
FrameOnEvent.prototype.update = function (dt) {

};

