// iframe-plane.js
var IframePlane = pc.createScript('iframePlane');
IframePlane.attributes.add('iframeUrl', { type: 'string' });
IframePlane.attributes.add("interactable", { type: "boolean" });
IframePlane.attributes.add('pixelsPerUnit', {
    type: 'number',
    default: 640,
    description: 'Number of canvas pixels per unit of world space. The larger the number, the higher the resolution of the iframe.'
});
IframePlane.attributes.add("closeImg", { type: "asset", assetType: "texture" });

IframePlane.attributes.add("pauseMethod", {
    type: "string",
    description: "Event body to fire to the iframe on pause."
});

// initialize code called once per entity
IframePlane.prototype.initialize = function () {
    // WARNING: IframePlane does not work with touch events

    var wrapper, close;

    if (this.iframeUrl) {
        wrapper = document.createElement("div");
        this.iframeElem = document.createElement("iframe");
        this.iframeElem.src = this.iframeUrl;
        this.iframeElem.style.border = '0px';
        this.iframeElem.style.height = '100%';
        this.iframeElem.style.width = '100%';
        close = document.createElement("div");
        close.addEventListener("click", () => this.entity.fire("zoom:out"));
        //close.src = this.closeImg.getFileUrl();
        close.style.position = "absolute";
        close.style.top = "0";
        close.style.right = "0";
        close.style.height = "100px";
        close.style.width = "100px";
        close.style.color = "white";
        wrapper.append(this.iframeElem);
        wrapper.append(close);
    } else {
        wrapper = null;
    }

    this._css3Plane = new pc.Css3Plane(wrapper, this.entity, this.pixelsPerUnit);
    this.setInteractable(this.interactable);

    var material = new pc.StandardMaterial();
    material.depthWrite = true;
    material.redWrite = false;
    material.greenWrite = false;
    material.blueWrite = false;
    material.alphaWrite = false;
    material.blendType = pc.BLEND_NONE;
    material.opacity = 0;
    material.update();

    this.entity.render.material = material;

    this.on('enable', function () {
        this._css3Plane.enable();
    }, this);

    this.on('disable', function () {
        this._css3Plane.disable();
    }, this);

    this.entity.on("iframe:interactable", this.setInteractable, this);

};

IframePlane.prototype.setInteractable = function (state) {
    if (this._css3Plane) {
        this._css3Plane.blockEvents(!state);
    }

    if (!state) {
        this.iframeElem.contentWindow.postMessage(this.pauseMethod, "*");
    }
};

