// cameraAnimation.js
var CameraAnimation = pc.createScript('cameraAnimation');

// initialize code called once per entity
CameraAnimation.prototype.initialize = function () {

    this.orbitPos = undefined;
    this.orbitRot = undefined;

    this.pos = new pc.Vec3();

};

// update code called every frame
CameraAnimation.prototype.update = function (dt) {

};

CameraAnimation.prototype.moveTo = function (target) {
    console.log("Moving camera to ", target);
    this.enableOrbitScripts(false);
    this.orbitPos = this.entity.getPosition().clone();
    this.orbitRot = this.entity.getEulerAngles().clone();

    this.pos = this.orbitPos.clone();
    this.rot = this.orbitRot.clone();

    this.app.tween(this.pos).to(target.getPosition(), 1, pc.Linear).on('update', function (dt) {
        this.entity.setPosition(this.pos);
    }, this).start();

    this.app.tween(this.rot).to(target.getEulerAngles(), 1, pc.Linear).on('update', function (dt) {
        this.entity.setEulerAngles(this.rot);
    }, this).start();
};

CameraAnimation.prototype.reset = function () {

    this.app.tween(this.pos).to(this.orbitPos, 1, pc.Linear).on('update', function (dt) {
        this.entity.setPosition(this.pos);
    }, this).on("complete", () => this.enableOrbitScripts(true), this).start();

    this.app.tween(this.rot).to(this.orbitRot, 1, pc.Linear).on('update', function (dt) {
        this.entity.setEulerAngles(this.rot);
    }, this).start();
};

CameraAnimation.prototype.enableOrbitScripts = function (bool) {
    this.entity.script.orbitCamera.enabled = bool;
    this.entity.script.mouseInput.enabled = bool;
    this.entity.script.touchInput.enabled = bool;

};

