// idle-animation-timer.js
var IdleAnimationTimer = pc.createScript('idleAnimationTimer');

IdleAnimationTimer.attributes.add("timeout", {
    type: "number",
    default: 10
});

IdleAnimationTimer.attributes.add("stopEvent", {
    type: "string",
    description: "Idle Animations won't play after this event has been fired."
});

IdleAnimationTimer.attributes.add("stopTarget", {
    type: "entity",
    description: "Target to listen for stopEvent. Will be this.app if left empty."
});

// initialize code called once per entity
IdleAnimationTimer.prototype.initialize = function () {
    this.timerInterval = setInterval(() => {
        this.entity.anim.setTrigger("Idle_Timer", true);
    }, this.timeout * 1000);
    let target = this.stopTarget ? this.stopTarget : this.app;
    target.on(this.stopEvent, () => clearInterval(this.timerInterval), this);
};

// update code called every frame
IdleAnimationTimer.prototype.update = function (dt) {

};

