// event-on-ui-click.js
var EventOnUiClick = pc.createScript('eventOnUiClick');

EventOnUiClick.attributes.add("eventName", {
    type: "string"
});

EventOnUiClick.attributes.add("target", {
    type: "entity"
});

// initialize code called once per entity
EventOnUiClick.prototype.initialize = function () {
    this.entity.element.on("click", () => {
        console.log("UI click");
        this.target.fire(this.eventName);
    }, this);
};

// update code called every frame
EventOnUiClick.prototype.update = function (dt) {

};

