// animEvent-to-event.js
var AnimEventToEvent = pc.createScript('animEventToEvent');

AnimEventToEvent.attributes.add('events', {
    title: 'Events',
    description: 'Events to listen to.',
    type: 'json',
    array: true,
    schema: [
        {
            name: "animEvent",
            type: "string"
        }, {
            name: "eventName",
            type: "string"
        }, {
            name: "target",
            type: "entity",
            description: "Target to fire the event on. If left empty, it is fired globaly on this.app ."
        }
    ]
});

// initialize code called once per entity
AnimEventToEvent.prototype.initialize = function () {
    for (let i = 0; i < this.events.length; i++) {
        var target = this.events[i].target ? this.events[i].target : this.app;
        this.entity.anim.on(this.events[i].animEvent, () => {
            target.fire(this.events[i].eventName);
        });
    }
};

// update code called every frame
AnimEventToEvent.prototype.update = function (dt) {

};

