// event-on-click.js
var EventOnClick = pc.createScript('eventOnClick');

EventOnClick.attributes.add("onDownEvent", {
    type: "string"
});

EventOnClick.attributes.add("onUpEvent", {
    type: "string"
});

EventOnClick.attributes.add("upTimeout", {
    type: "number",
    description: "Timeout in s for when the upEvent is ignored."
});

// initialize code called once per entity
EventOnClick.prototype.initialize = function () {

    this.heldDown = false;

    this.app.mouse.on(pc.EVENT_MOUSEDOWN, this.onDown, this);

    this.app.mouse.on(pc.EVENT_MOUSEUP, this.onUp, this);

    this.on('destroy', function () {
        this.app.mouse.off(pc.EVENT_MOUSEDOWN, this.onDown, this);
        this.app.mouse.off(pc.EVENT_MOUSEUP, this.onUp, this);

    }, this);

};

EventOnClick.prototype.onDown = function (e) {
    this.heldDown = true;
    this.downTimer = setTimeout(() => this.heldDown = false, this.upTimeout * 1000);

    var from = this.entity.camera.screenToWorld(e.x, e.y, this.entity.camera.nearClip);
    var to = this.entity.camera.screenToWorld(e.x, e.y, this.entity.camera.farClip);

    var result = this.app.systems.rigidbody.raycastFirst(from, to);
    if (result) {
        result.entity.fire(this.onDownEvent);
    }
};


EventOnClick.prototype.onUp = function (e) {
    if (!this.heldDown)
        return;

    clearTimeout(this.downTimer);

    var from = this.entity.camera.screenToWorld(e.x, e.y, this.entity.camera.nearClip);
    var to = this.entity.camera.screenToWorld(e.x, e.y, this.entity.camera.farClip);

    var result = this.app.systems.rigidbody.raycastFirst(from, to);
    if (result) {
        result.entity.fire(this.onUpEvent);
    }
};

// update code called every frame
EventOnClick.prototype.update = function (dt) {

};

