// soundOnEvent.js
var SoundOnEvent = pc.createScript('soundOnEvent');

SoundOnEvent.attributes.add('events', {
    title: 'Events',
    description: 'Events to listen to.',
    type: 'json',
    array: true,
    schema: [
        {
            name: "eventName",
            type: "string"
        }, {
            name: "src",
            type: "entity",
            description: "Source to listen the event on. If left empty, it is listend globaly on this.app ."
        }, {
            name: "soundSlot",
            type: "string",
            description: "Name of the soundSlot to play."
        }
    ]
});


// initialize code called once per entity
SoundOnEvent.prototype.initialize = function () {
    var soundSrc = this.entity.sound;
    for (let i = 0; i < this.events.length; i++) {

        var src = this.events[i].src ? this.events[i].src : this.app;
        src.on(this.events[i].eventName, () => {

            soundSrc.play(this.events[i].soundSlot);
        });
    }
};

// update code called every frame
SoundOnEvent.prototype.update = function (dt) {

};

