// parallax-cam.js
var ParallaxCam = pc.createScript('parallaxCam');

ParallaxCam.attributes.add("camEntity", {
    type: "entity"
});

ParallaxCam.attributes.add("maxTiltX", {
    type: "number"
});

ParallaxCam.attributes.add("maxTiltY", {
    type: "number"
});

ParallaxCam.attributes.add("speed", {
    type: "number",
    default: 1
});

// initialize code called once per entity
ParallaxCam.prototype.initialize = function () {
    this.app.mouse.on(pc.EVENT_MOUSEMOVE, this.onMouseMove, this);
    this.pivot = this.entity.parent;
};

ParallaxCam.prototype.onMouseMove = function (event) {
    let tiltY = event.x / this.app.graphicsDevice.width * this.app.graphicsDevice.maxPixelRatio - 0.5;
    let tiltX = event.y / this.app.graphicsDevice.height * this.app.graphicsDevice.maxPixelRatio - 0.5;
    //console.log(tiltX, tiltY);

    this.tiltTarget = new pc.Vec3(-this.maxTiltX * tiltX, -this.maxTiltY * tiltY, 0);
};

// update code called every frame
ParallaxCam.prototype.update = function (dt) {
    if (this.tiltTarget) {
        let target = new pc.Vec3();

        this.pivot.setLocalEulerAngles(target.lerp(this.pivot.getLocalEulerAngles(), this.tiltTarget, dt * this.speed));

    }

};

