// html-loader.js
var HtmlLoader = pc.createScript('htmlLoader');

HtmlLoader.attributes.add('html', { type: 'asset', assetType: 'html', title: 'Html Asset' });

HtmlLoader.attributes.add('cssAssets', { type: 'asset', assetType: 'css', array: true, title: 'Css Assets' });

HtmlLoader.attributes.add("externalScripts", {
    type: "asset",
    array: true
});

HtmlLoader.attributes.add('binderScriptName', {
    type: 'string',
    title: 'Binder Script Name'
});

HtmlLoader.attributes.add('imgAssets', {
    type: "json",
    array: true,
    schema: [{
        name: "class",
        type: "string"
    }, {
        name: "img",
        type: "asset"
    }]
});

HtmlLoader.attributes.add('imgUrls', {
    type: "json",
    array: true,
    schema: [{
        name: "class",
        type: "string"
    }, {
        name: "url",
        type: "string"
    }]
});


// initialize code called once per entity
HtmlLoader.prototype.initialize = function () {
    this.htmlElement = document.createElement('div');
    this.htmlElement.innerHTML = this.html.resource;
    this.htmlElement.style.opacity = 0;
    document.body.appendChild(this.htmlElement);

    this.on("destory", function () {
        this.htmlElement.remove();
    });
    this.loadCss();
    this.bindScripts();

    this.loadImg();
};

// update code called every frame
HtmlLoader.prototype.update = function (dt) {

};

HtmlLoader.prototype.loadImg = function () {
    var self = this;
    for (var i = 0; i < this.imgAssets.length; i++) {
        let elem = document.getElementById(this.imgAssets[i].class);
        if (elem) {
            elem.src = this.imgAssets[i].img.getFileUrl();
        } else
            document.querySelectorAll("." + this.imgAssets[i].class).forEach((img) => {
                img.src = this.imgAssets[i].img.getFileUrl();

            }, this);
    }
    for (var i = 0; i < this.imgUrls.length; i++) {
        let elem = document.getElementById(this.imgUrls[i].class);
        if (elem) {
            elem.src = this.imgUrls[i].url;
        } else
            document.querySelectorAll("." + this.imgUrls[i].class).forEach((img) => {
                img.src = this.imgUrls[i].url;

            }, this);
    }
};

HtmlLoader.prototype.loadCss = function () {
    this.counter = 0;
    for (var i = 0; i < this.cssAssets.length; i++) {
        var css = document.createElement("link");
        css.rel = "stylesheet";
        css.type = "text/css";
        css.onload = () => {
            this.counter++;
            this.onLoaded();
        };
        css.href = this.cssAssets[i].getFileUrl();
        document.head.appendChild(css);
        console.log(css);
        this.on("destroy", function () {
            css.remove();
        }, this);
    }
    this.onLoaded();
};

HtmlLoader.prototype.onLoaded = function () {
    if (this.counter >= this.cssAssets.length) {
        this.htmlElement.style.opacity = 1;
    }
};

HtmlLoader.prototype.bindScripts = function () {
    var promises = [];

    for (var i = 0; i < this.externalScripts.length; i++) {

        console.log(this.externalScripts[i]);
        var url = this.externalScripts[i].getFileUrl();
        const script = document.createElement('script');
        script.src = url;

        const promise = new Promise((resolve, reject) => {
            script.addEventListener("load", () => {
                console.log("Script " + script + " loaded");
                resolve();
            });
        });
        promises.push(promise);


        document.head.appendChild(script);

        this.on('destroy', function () {
            script.remove();
        });
    }


    Promise.all(promises).then(() => {
        if (this.binderScriptName != "") {
            this.scriptBinder = this.entity.script.get(this.binderScriptName);

            this.scriptBinder.bindEvents(this.htmlElement);
        }
    });
};

