// animateOnEvent.js
var AnimateOnevent = pc.createScript('animateOnevent');

AnimateOnevent.attributes.add("eventToListenTo", {
    description: "Event that this entity should listen for.",
    type: "string"
});

AnimateOnevent.attributes.add("animEntity", {
    description: "Entity with the anim component 'animTriggerName' should be activated on",
    type: "entity"
});

AnimateOnevent.attributes.add("animTriggerName", {
    description: "The name of the Trigger that should be activated on 'animEntity'",
    type: "string"
});

// initialize code called once per entity
AnimateOnevent.prototype.initialize = function () {
    this.entity.on(this.eventToListenTo, () => {
        this.animEntity.anim.setTrigger(this.animTriggerName, true);
        this.app.fire("idle:stop");
        //console.log("Animate");
    }, this);
};

// update code called every frame
AnimateOnevent.prototype.update = function (dt) {

};

