// event-to-events.js
var EventToEvents = pc.createScript('eventToEvents');
EventToEvents.attributes.add("inputEvent", {
    description: "Event that 'inputTarget' listens for.",
    type: "string"
});
EventToEvents.attributes.add("inputTarget", {
    description: "Entity that listens for 'inputEvent'.",
    type: "entity"
});

EventToEvents.attributes.add("outputEvents", {
    type: "json",
    array: true,
    schema: [{
        name: "event",
        type: "string"
    }, {
        name: "target",
        type: "entity"
    }]
});

// initialize code called once per entity
EventToEvents.prototype.initialize = function () {
    let input = this.inputTarget ? this.inputTarget : this.app;

    input.on(this.inputEvent, () => {
        this.outputEvents.forEach((e) => {
            let output = e.target ? e.target : this.app;
            output.fire(e.event);
        });
    }, this);
};

// update code called every frame
EventToEvents.prototype.update = function (dt) {

};

