// event-to-event.js
var EventToEvent = pc.createScript('eventToEvent');
EventToEvent.attributes.add("inputEvent", {
    description: "Event that 'inputTarget' listens for.",
    type: "string"
});
EventToEvent.attributes.add("inputTarget", {
    description: "Entity that listens for 'inputEvent'.",
    type: "entity"
});
EventToEvent.attributes.add("outputEvent", {
    description: "Event that should be fired on 'outputTarget' when 'inputTarget' receives 'inputEvent'.",
    type: "string"
});
EventToEvent.attributes.add("outputTarget", {
    description: "Entity that 'outputEvent' is fired on.",
    type: "entity"
});

// initialize code called once per entity
EventToEvent.prototype.initialize = function () {
    let input = this.inputTarget ? this.inputTarget : this.app;
    let output = this.outputTarget ? this.outputTarget : this.app;

    input.on(this.inputEvent, () => output.fire(this.outputEvent), this);
};

// update code called every frame
EventToEvent.prototype.update = function (dt) {

};

