// event-on-key.js
var EventOnKey = pc.createScript('eventOnKey');

EventOnKey.attributes.add("keycode", {
    type: "number",
    description: "Keycode for key to listen for. Check https://keycode.info/ for info."
});

EventOnKey.attributes.add("eventName", {
    type: "string"
});

EventOnKey.prototype.initialize = function () {
    this.app.keyboard.on(pc.EVENT_KEYDOWN, this.onKeyDown, this);

    this.on('destroy', function () {
        this.app.keyboard.off(pc.EVENT_KEYDOWN, this.onKeyDown, this);
    }, this);
};

EventOnKey.prototype.onKeyDown = function (e) {
    console.log(e.key);
    if (e.key == this.keycode) {
        console.log("FIRE!");
        this.entity.fire(this.eventName);
    }
};

// update code called every frame
EventOnKey.prototype.update = function (dt) {

};

