// anim-trigger.js
var AnimTrigger = pc.createScript('animTrigger');

AnimTrigger.attributes.add("triggers", {
    type: "string",
    array: true
});

// initialize code called once per entity
AnimTrigger.prototype.initialize = function () {
    for (let i = 0; i < this.triggers.length; i++) {
        this.entity.on(this.triggers[i], () => {
            this.entity.anim.setTrigger(this.triggers[i], true);
        }, this);
    }
};

// update code called every frame
AnimTrigger.prototype.update = function (dt) {

};

